export type CxArg =
  | undefined
  | null
  | string
  | boolean
  | Record<string, boolean | null | undefined>
  | readonly CxArg[]

/** Copy pasted from
 * https://github.com/emotion-js/emotion/blob/23f43ab9f24d44219b0b007a00f4ac681fe8712e/packages/react/src/class-names.js#L17-L63
 **/
export const classnames = (args: CxArg[]): string => {
  const len = args.length
  let i = 0
  let cls = ''
  for (; i < len; i++) {
    const arg = args[i]
    if (arg == null) {
      continue
    }

    let toAdd
    switch (typeof arg) {
      case 'boolean':
        break
      case 'object': {
        if (Array.isArray(arg)) {
          toAdd = classnames(arg)
        } else {
          const obj = arg as Record<string, boolean | null | undefined>
          toAdd = ''
          for (const k in obj) {
            if (obj[k] && k) {
              if (toAdd) {
                toAdd += ' '
              }
              toAdd += k
            }
          }
        }
        break
      }
      default: {
        toAdd = arg
      }
    }
    if (toAdd) {
      if (cls) {
        cls += ' '
      }
      cls += toAdd
    }
  }
  return cls
}
